@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;700&display=swap);
body {
  margin: 0;
  background: #000000;
  background-repeat: no-repeat;
  font-family: 'Roboto', sans-serif;
}


.principal {
    display: grid;
    justify-content: center;
    margin: 0;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 50px 0px;
}

.children {
    border: 2px solid #000000;
    border-radius: 10px;
}

img {
    border-radius: 10px 10px 0 0;
}

.info-div {
    background: rgb(0, 0, 0, 0.95);
    width: 333px;
    padding: 5px 0px;
    margin: -5px 0;
    border-radius: 0 0 10px 10px;
}

.info-div h1 {
    font-size: 40px;
    font-weight: 700;
    color: #a71930;
}

.info-div h3 {
    color: #a71930;
    font-weight: 400;
}

.info-div h3 span {
    color: #a5acaf;
}

.info-div div p {
    color: #a5acaf;
    font-weight: 300;
    line-height: 30px;
}

.info-div div {
    margin: 10px;
}
form {
    text-align: center;
    padding: 10px 0;
}

input {
    outline: none;
    border: 1px solid #a71930;
    border-radius: 20px;
    padding: 10px;
    font-weight: 700;
    color: #000000;
    font-family: 'Roboto', sans-serif;
}


button {
    background-color: #a71930;
    outline: none;
    border: 1px solid #a71930;
    border-radius: 20px;
    padding: 10px;
    margin: 0 5px;
    font-weight: 700;
}

@media screen and (min-width: 1024px) {
    .principal div {
        display: flex;
    }
    .info-div {
        min-width: 500px;
        border-radius: 0 10px 10px 0;
        margin: 0;
    }
    .info-div div {
        display: block;
    }
    img {
        border-radius: 10px 0 0 10px;
    }
}

.home {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
}

.title-div {
  color: white;
  text-align: center;
  margin: 20px 0 50px 0;
}

.title-div h1 {
  font-size: 50px;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  text-decoration: underline;
  -webkit-text-decoration-color: #a71930;
          text-decoration-color: #a71930;
}

@media screen and (min-width: 1024px) {
  .home {
    margin-top: 130px;
  }

  .title-div {
    text-align: start;
    padding-right: 100px;
    margin: 50px;
  }

  .home-img {
    width: 500px;
  }
}

.about {
  color: white;
  margin: 70px 0;
  text-align: center;
}

.about h1 {
  font-weight: 700;
}

.about p {
  line-height: 25px;
  font-weight: 300;
}

.about p a {
  color: #a71930;
  text-decoration: none;
}

.about p a:hover {
  text-decoration: underline;
}

.social {
  margin-top: 100px;
}

.icon {
  margin: 0px 10px;
}

@media screen and (min-width: 1024px) {
  .about h1 {
    font-size: 60px;
  }
  .about p {
    font-size: 20px;
  }
}

.nav {
  display: flex;
  margin: 30px 0;
  justify-content: center;
}

.item-nav {
  margin: 0 20px;
  list-style: none;
  font-weight: 700;
}

.link-nav:hover {
  background-color: white;
  color: #000000;
}

.link-nav {
  text-decoration: none;
  color: #89958D;
  padding: 10px 10px;
  border-radius: 20px;
}

